.page-footer {
  background-color: $color-grey-200;
}

.page-footer__wrapper {
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding-top: 20px;
    padding-bottom: 20px;
    align-items: center;
  }
}

.page-footer__logo {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  padding-top: 20px;
  border-bottom: 1px solid $color-grey-300;

  @media (min-width: $tablet-width) {
    padding-bottom: 0;
    padding-top: 0;
    border-bottom: none;
    justify-content: left;
  }
}
